































































































































import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import {
  distanceInWords,
  distanceInWordsToNow,
  parse,
  distanceInWordsStrict
} from "date-fns";
import { settingsModule, adminModule } from "@/store/modules";
import { Language, LogFromApi, LogType } from "@/shared";
import enLocale from "date-fns/locale/en";
import deLocale from "date-fns/locale/de";
import { dateToYYYYMMDDString } from "@/components/misc/JobExecutionCalendar.vue";
import { getLogs, getLogDetails, getLogDetailsUrl, baseUrl } from "@/store/api";
import Logo from "@/components/misc/Logo.vue";

export interface IBackupEvent {
  id: number | string;
  text?: string;
  time?: string;
  project?: any;
  color?: string;
  heading?: string;
  email?: boolean;
  error?: boolean;
  chips?: ({ name: string; class?: string })[];
  dotColor?: string;
  date?: Date;
  isSectionHeading?: boolean;
  actions?: ({ text: string })[];
}

interface IDetailedBackupEvent extends IBackupEvent {
  fullText: string;
}

@Component({
  components: {
    Logo
  }
})
export default class Timeline extends Mixins() {
  dialogOpen = false;
  currentEventDetails: IDetailedBackupEvent | null = null;
  loading = false;
  noMoreEntries = false;

  get logDetailsUrl() {
    return (
      this.currentEventDetails &&
      `${baseUrl}${getLogDetailsUrl(this.currentEventDetails.id)}`
    );
  }

  async showEventDetails(event: IBackupEvent) {
    this.dialogOpen = true;
    console.log(event.id);
    try {
      const response = await getLogDetails(Number(event.id));
      if (response) {
        this.currentEventDetails = {
          ...event,
          fullText: response
        };
        return;
      }
    } catch (err) {
      console.log(err);
    }
    this.dialogOpen = false;
  }

  get computedEvents(): IBackupEvent[] {
    let lastDate = "";
    return [...this.events].flatMap(ev => {
      const ret: IBackupEvent[] = [];
      const event: IBackupEvent = {
        ...ev,
        time: ev.date && this.$d(ev.date, "time")
      };
      if (event.date) {
        const newDate = dateToYYYYMMDDString(event.date);
        if (newDate !== lastDate) {
          lastDate = newDate;
          const heading = (
            this.$d(parse(newDate), "short") +
            " - " +
            distanceInWordsStrict(new Date(), newDate, {
              locale:
                settingsModule.language === Language.de ? deLocale : enLocale,
              addSuffix: true,
              unit: "d"
            })
          ).toUpperCase();
          return [
            {
              id: heading,
              isSectionHeading: true,
              heading
            },
            event
          ];
        }
      }
      return event;
    });
  }

  events: IBackupEvent[] = [];

  interval = 0;

  @Prop({
    type: String,
    required: true,
    validator: (arg: string) =>
      ([
        LogType.other,
        LogType.client,
        LogType.repository,
        LogType.backupJob
      ] as string[]).indexOf(arg) !== -1
  })
  type!: LogType;

  @Prop({
    type: [Number, String],
    default: 0
  })
  jobId!: number;

  @Prop({
    type: [Number, String],
    default: 0
  })
  repoId!: number;

  @Prop({
    type: [Number, String],
    default: 0
  })
  clientId!: number;

  @Watch("type", { immediate: true })
  async fetchLogs() {
    const limit = 4;
    const offset =
      (this.events.length && Number(this.events[this.events.length - 1].id)) ||
      undefined;
    this.loading = true;
    try {
      const response = await getLogs(
        (this.type === LogType.other && {
          limit,
          type: LogType.other,
          offset
        }) ||
          (this.type === LogType.client && {
            limit,
            type: LogType.client,
            clientId: this.clientId,
            offset
          }) ||
          (this.type === LogType.backupJob && {
            limit,
            type: LogType.backupJob,
            jobId: this.jobId,
            offset
          }) || {
            limit,
            type: LogType.repository,
            repoId: this.repoId,
            offset
          }
      );
      if (response.payload) {
        this.events.push(
          ...response.payload.logs.map(this.transformLogFromApiToInternal)
        );
        if (response.payload.logs.length !== limit) {
          this.noMoreEntries = true;
        }
      }
    } catch (error) {
      console.log(error);
    }
    this.loading = false;
  }

  transformLogFromApiToInternal(api: LogFromApi): IBackupEvent {
    const chips: ({ name: string; class?: string })[] = [];
    if (api.type === LogType.client) {
      const client = adminModule.clientbyID(api.clientId);
      if (client) {
        chips.push({
          name: client.name,
          class: "orange"
        });
      }
    }
    if (api.type === LogType.repository || api.type === LogType.backupJob) {
      const repo = adminModule.clientbyID(api.repoId);
      if (repo) {
        chips.push({
          name: repo.name,
          class: "green"
        });
      }
    }
    if (api.type === LogType.backupJob) {
      const job = adminModule.clientbyID(api.jobId);
      if (job) {
        chips.push({
          name: job.name,
          class: "blue"
        });
      }
    }
    return {
      id: api.id,
      heading: api.eventDescription,
      dotColor: api.logLevel,
      date: parse(api.date),
      chips
    };
  }

  mounted() {
    // // TODO: check for new logs
    // this.interval = setInterval(async () => {
    // }, 5000);
  }

  beforeDestroy() {
    // clearInterval(this.interval);
  }
}
